@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap');

body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  background-color: #f2f4f8;
  color: #222;
}

.App {
  text-align: center;
  padding: 2rem;
}

header {
  background-color: #1f2937;
  color: white;
  padding: 3rem 1rem;
  border-bottom: 4px solid #34a853;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
}

header h1 {
  margin: 0;
  font-size: 3rem;
}

header p {
  font-size: 1.25rem;
  margin-top: 0.5rem;
}

header a {
  color: #3b82f6;
  margin: 0 0.5rem;
  text-decoration: none;
  font-weight: 600;
}

header a:hover {
  text-decoration: underline;
}

section {
  margin-top: 3rem;
  padding: 2rem;
  background: white;
  border-radius: 12px;
  max-width: 720px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.05);
  transition: transform 0.2s ease;
}

section:hover {
  transform: translateY(-4px);
}

section h2 {
  color: #1f2937;
  margin-bottom: 1rem;
}

section a {
  color: #2563eb;
  text-decoration: none;
  font-weight: 600;
}

section a:hover {
  text-decoration: underline;
}

hr.green-line {
  border: none;
  height: 6px;
  width: 100%;
  max-width: 680px;
  background-color: #34a853;
  margin: 2rem auto;
  border-radius: 3px;
}
